<template>
  <div class="flex justify-center">
    <!--  Container  -->
    <ul class="progress-circle">
      <!--  Item  -->
      <div
        class="progress-circle"
        :data-name="title"
        :data-percent="percent"
        ref="progress"
      >
        <svg :height="radius * 2" :width="radius * 2">
          <defs>
            <linearGradient id="gradient">
              <stop offset="10%" stop-color="#ff00ff " />
              <stop offset="95%" stop-color="#19f6e8" />
            </linearGradient>
          </defs>

          <circle
            stroke="url('#gradient')"
            :stroke-dasharray="circumference + ' ' + circumference"
            :style="{ strokeDashoffset: strokeDashoffset }"
            :stroke-width="normalizedStroke"
            fill="transparent"
            :r="normalizedRadius"
            :cx="radius"
            :cy="radius"
          />
        </svg>
      </div>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    radius: Number,
    progress: Number,
    stroke: Number,
    percent: String,
    title: String
  },
  data() {
    const normalizedRadius = this.radius - this.stroke * 2;
    const circumference = normalizedRadius * 2 * Math.PI;
    const normalizedStroke = parseInt(this.stroke);
    const progressInterval = null;

    return {
      normalizedRadius,
      normalizedStroke,
      circumference,
      progressInterval
    };
  },
  computed: {
    strokeDashoffset() {
      return this.circumference - (this.progress / 100) * this.circumference;
    }
  },
  mounted() {
    let i = 0;
    this.progressInterval = setInterval(() => this.increasePercent(i++), 50);
  },
  methods: {
    increasePercent(i) {
      if (i <= this.progress) {
        if (this.$refs.progress) {
          this.$refs.progress.dataset.percent = `${i}%`;
        }
      } else {
        clearInterval(this.progressInterval.progressInterval);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/core";
.progress-circle {
  display: inline-block;
  position: relative;
  text-align: center;
  color: $white;
  font-weight: 100;
  padding: 0;
  margin: 10px;
  &:before {
    content: attr(data-name);
    position: absolute;
    width: 100%;
    top: -1em;
    font-weight: 800;
    font-size: 20px;
  }
  &:after {
    content: attr(data-percent);
    position: absolute;
    width: 100%;
    top: 100px;
    left: 0;
    font-size: 1.5em;
    text-align: center;
  }
}

svg {
  stroke-dashoffset: 0;
  animation: dash 5s linear forwards;
  animation-delay: 8s;
}

@keyframes dash {
  from {
    stroke-dashoffset: 270;
  }
}

circle {
  -webkit-transition: stroke-dashoffset 0.35s;
  transition: stroke-dashoffset 0.35s;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  animation: dash 5s forwards;
}
</style>
