<template>
  <div class="vue-element-timeline show-animation">
    <div
      v-for="(company, index) of companies"
      :key="index"
      class="vue-element-timeline__item item-show"
    >
      <div class="vue-element-timeline__date__wrapper">
        <div class="vue-element-timeline__hexagon">
          <svg
            :style="{ fill: company.fill }"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 177.4 197.4"
          >
            <path
              d="M0,58.4v79.9c0,6.5,3.5,12.6,9.2,15.8l70.5,40.2c5.6,3.2,12.4,3.2,18,0l70.5-40.2c5.7-3.2,9.2-9.3,9.2-15.8V58.4 c0-6.5-3.5-12.6-9.2-15.8L97.7,2.4c-5.6-3.2-12.4-3.2-18,0L9.2,42.5C3.5,45.8,0,51.8,0,58.4z"
            />
          </svg>
        </div>
        <div class="vue-element-timeline__hexagon">
          <svg
            :style="{ fill: company.fill, filter: getFilter(company.fill) }"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 177.4 197.4"
          >
            <path
              d="M0,58.4v79.9c0,6.5,3.5,12.6,9.2,15.8l70.5,40.2c5.6,3.2,12.4,3.2,18,0l70.5-40.2c5.7-3.2,9.2-9.3,9.2-15.8V58.4 c0-6.5-3.5-12.6-9.2-15.8L97.7,2.4c-5.6-3.2-12.4-3.2-18,0L9.2,42.5C3.5,45.8,0,51.8,0,58.4z"
            />
          </svg>
        </div>
        <h4 class="vue-element-timeline__date" :style="dateColor(company)">
          {{ company.year }}
        </h4>
      </div>
      <div
        class="vue-element-timeline__content"
        :style="{ '--accent': company.fill }"
      >
        <figure class="vue-element-timeline__content--image">
          <img :src="getImage(company.logo)" :alt="imageAlt(company.title)" />
        </figure>
        <h5 class="vue-element-timeline__content--title">
          {{ company.title }}
        </h5>
        <p class="vue-element-timeline__content--description">
          {{ company.description }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    companies: {
      type: Array,
      required: false
    }
  },
  methods: {
    dateColor(company) {
      return {
        color: company.fill
      };
    },
    getFilter(companyColor) {
      const color = this.hexToRgb(companyColor);
      return `drop-shadow(4px 5px 4px rgba(${color.r},${color.g},${color.b},0.3))`;
    },
    hexToRgb(hex) {
      var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
      return result
        ? {
            r: parseInt(result[1], 16),
            g: parseInt(result[2], 16),
            b: parseInt(result[3], 16)
          }
        : null;
    },
    getImage(logo) {
      return require(`../assets/logos/mono/${logo}.png`);
    },
    imageAlt(title) {
      return `${title} Logo`;
    }
  }
};
</script>

