<template>
  <section
    id="technologies"
    class="technologies-section container vh-100"
    v-observe-visibility="visibilityChanged"
  >
    <SectionHeader
      title="Frameworks"
      description="These are some technologies, rated by the level of confidence that I feel in each one"
    ></SectionHeader>
    <div>
      <ul>
        <li v-for="tech of technologies" :key="tech.id">
          <img :src="getImage(tech.id)" :alt="tech.title" width="60" />
          <ProgressCircle
            v-if="isVisible"
            class="progress-circle"
            :title="tech.title"
            :percent="percentage(tech.progress)"
            :radius="80"
            :progress="tech.progress"
            :stroke="7"
          />
          <div v-else class="placeholder"></div>
        </li>
      </ul>
    </div>
  </section>
</template>
<script>
import SectionHeader from "@/components/SectionHeader.vue";
import ProgressCircle from "@/components/ProgressCircle.vue";
export default {
  components: {
    ProgressCircle,
    SectionHeader
  },
  data() {
    return {
      technologies: [
        { id: "vue", title: "Vue", progress: 100 },
        { id: "angular", title: "Angular", progress: 95 },
        { id: "ionic", title: "Ionic", progress: 90 },
        { id: "react", title: "React", progress: 85 },
        { id: "native", title: "React Native", progress: 80 },
        { id: "flutter", title: "Flutter", progress: 70 }
      ]
    };
  },
  computed: {
    isVisible() {
      return this.$store.getters.getTechnologiesIsVisible;
    }
  },
  methods: {
    visibilityChanged(visibility) {
      this.$store.dispatch("setSectionVisible", {
        section: "technologies",
        visibility
      });
    },
    percentage(progress) {
      return `${progress}%`;
    },
    getImage(logo) {
      return require(`../assets/technologies/${logo}.png`);
    }
  }
};
</script>

