<template>
  <form @submit="sendForm">
    <h1>{{ status }}</h1>
    <div>
      <label for="name">Name</label>
      <input type="text" id="name" name="name" v-model="contactForm.name" />
    </div>

    <div>
      <label for="email">Email</label>
      <input
        type="email"
        id="email"
        name="_replyto"
        v-model="contactForm._replyto"
      />
    </div>

    <div>
      <label for="subject">Subject</label>
      <input
        type="subject"
        id="subject"
        name="subject"
        v-model="contactForm.subject"
      />
    </div>

    <div>
      <label for="message">Message</label>
      <textarea
        name="message"
        id="message"
        cols="30"
        rows="7"
        placeholder="Leave your message here..."
        v-model="contactForm.message"
      ></textarea>
    </div>

    <div>
      <button id="submitContactForm" type="submit" hidden></button>
      <NeonButton title="Send" @click="sendClick"></NeonButton>
    </div>
  </form>
</template>

<script>
import NeonButton from "@/components/NeonButton";

export default {
  components: {
    NeonButton
  },
  data() {
    return {
      contactForm: {
        name: null,
        _replyto: null,
        subject: null,
        message: null
      },
      status: null
    };
  },
  methods: {
    sendClick() {
      const submit = document.getElementById("submitContactForm");
      submit.click();
    },
    sendForm(ev) {
      ev.preventDefault();
      let data = new FormData();

      const xhr = new XMLHttpRequest();

      for (var key in this.contactForm) {
        data.append(key, this.contactForm[key]);
      }

      xhr.open("POST", "https://formspree.io/xjvewkev");
      xhr.setRequestHeader("Accept", "application/json");
      xhr.onreadystatechange = () => {
        if (xhr.readyState !== XMLHttpRequest.DONE) return;
        if (xhr.status === 200) {
          this.contactForm = {
            name: null,
            _replyto: null,
            subject: null,
            message: null
          };
          this.status =
            "Thanks for your message, I will be in touch as soon as possible";
        } else {
          this.status =
            "Sorry, something went wrong trying to send your message. Try again soon";
        }
      };
      xhr.send(data);
    }
  }
};
</script>

