<template>
  <section id="contact" class="" v-observe-visibility="visibilityChanged">
    <h1 hidden>Contact Us</h1>
    <figure class="bgimg bgimg-first centered-content">
      <ContactForm />
    </figure>
  </section>
</template>

<script>
import ContactForm from "@/components/ContactForm.vue";
export default {
  components: {
    ContactForm
  },
  computed: {
    isVisible() {
      return this.$store.getters.getContactIsVisible;
    }
  },
  methods: {
    visibilityChanged(visibility) {
      this.$store.dispatch("setSectionVisible", {
        section: "contact",
        visibility
      });
    }
  }
};
</script>

